.dialogs {
    display: grid;
    grid-template-columns: 1fr 3fr;
}

.dialogs_items {
    border: 1px solid yellow;
    margin-right: 10px;
}

.messages {
    /*padding: 10px;*/
    border: 1px solid yellow;
    height: 640px;
}

.blockMessage{
    height: calc(100% - 60px);
    overflow: auto;
}